import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col, message } from 'antd';
import { getListPaperType, deletePaperType } from 'services/PaperType';



class PaperType extends Component {
  state = {
    dataTable: [],
    pagination: {},
    page: 0,
    loading: false,
    IsPagingEnabled: true
  };

  componentDidMount() {
    this.getListPaperTypetable()
  }

  handleTableChange = (pagination, filters, sorter) => {
    const {IsPagingEnabled} = this.state;
    this.setState({
      page: pagination.current,
    });
    this.getListPaperTypetable(pagination.current,IsPagingEnabled)
  }

  getListPaperTypetable = (page, IsPagingEnabled) => {
    this.setState({
      loading: true,
    })
    getListPaperType(page,IsPagingEnabled)
      .then((res) => {
        const pagination = { ...this.state.pagination };
        const {items} = res.data;
        pagination.total = res.data.totalResults;
        items.forEach((item)=> {
          if(item.typeId ===1) {
            item.typeId = 'Tuyển sinh'
          } else {
            item.typeId = 'Nhập học'
          }
        })
        this.setState({
          dataTable: res.data.items,
          pagination,
          loading: false,
        })
      })
      .catch((error) => {
        console.log('====>', error);
        this.setState({
          loading: false,
        })
      })
  }

  confirmDelete = (e) => {
    this.setState({
      loading: true,
    })
    deletePaperType(e)
      .then(() => {
        message.success('Xóa giấy tờ thành công');
        this.getListPaperTypetable();
        this.setState({
          loading: false,
        })
      })
      .catch((error) => {
        console.log('res', error);
        message.error(error.response.data.message);
        this.setState({
          loading: false,
        })
      });
  }

  cancel = (e) => {
  }

  renderTable = () => {
    const { dataTable } = this.state;
    const columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên loại giấy tờ',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Loại giấy tờ',
        dataIndex: 'typeId',
        key: 'typeId',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          dataTable.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#PaperType/form/edit/${record.id}`} />
              <Popconfirm title="Bạn có chăc chắn muốn xóa" onConfirm={() => this.confirmDelete(record.id)} onCancel={this.cancel} okText="Xóa" cancelText="Hủy">
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
    return <Table 
      loading={this.state.loading} 
      bordered dataSource={this.state.dataTable} 
      columns={columns}  
      pagination={this.state.pagination}
      onChange={this.handleTableChange}
      />
  }

  render() {
    return (
      <Card title="Danh mục loại giấy tờ">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/PaperType/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            {this.renderTable()}
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <PaperType />
  </Page>
);



